.Error {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: large;
  font-family: 'Open Sans', sans-serif;
  height: inherit;
  justify-content: center;
  text-align: center;
  width: inherit;
}

.Error-Message {
  color: var(--font-color-app);
  margin: 40px 20px 40px 20px;
  font-size: larger;
}

.Error-Label span {
  color: var(--font-color-app);
  font-size: medium;
  padding: 10px 20px;
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  /* transition: all 0.5s; */
  /* cursor: pointer; */
  -webkit-animation-name: errorBackgroundAnimation;
          animation-name: errorBackgroundAnimation;
  -webkit-animation: errorBackgroundAnimation 4s alternate infinite;
          animation: errorBackgroundAnimation 4s alternate infinite;
}

@-webkit-keyframes errorBackgroundAnimation {
  33% {
    background: var(--color-app);
  }
  66% {
    background: var(--background-color);
  }
  99% {
    background: var(--color-app);
  }
}

@keyframes errorBackgroundAnimation {
  33% {
    background: var(--color-app);
  }
  66% {
    background: var(--background-color);
  }
  99% {
    background: var(--color-app);
  }
}

.ErrorButton {
  /* position: relative; */
  background: var(--color-app) !important;
  color: var(--font-color-app) !important;
  border-color: var(--color-app) !important;
  padding: 10px 20px;
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  transition: all 0.5s;
  cursor: pointer;
  font-size: small !important;
  box-shadow: 5px 5px 2px #000;
  -moz-box-shadow: 5px 5px 2px #000;
  -webkit-box-shadow: 5px 5px 2px #000;
  -khtml-box-shadow: 5px 5px 2px #000;
  border-radius: 5px;
}

.ErrorButton:hover {
  background: var(--dark-color-app) !important;
  color: var(--font-color-app) !important;
  -webkit-animation: jelly 0.5s !important;
  animation: jelly 0.5s !important;
}

.Header {
  width: 100%;
  justify-content: 'center';
  margin-top: 10px;
  margin-bottom: 20px;
}

.Loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--font-color-app);
  letter-spacing: 1rem;
  font-size: 120%;
  font-family: Lato, sans-serif;
  text-transform: uppercase;
}

.Loader-Span {
  -webkit-animation: blur 1.5s alternate infinite;
          animation: blur 1.5s alternate infinite;
}

@-webkit-keyframes blur {
  to {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
}

@keyframes blur {
  to {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
}

.SearchResults {
  /* width: max-content; */
  margin-top: 15px;
  text-align: center;
  display: block;
  align-items: center;
  /* border: solid; */
  width: auto;
  overflow: auto;
  padding-top: 10px;
}
.SearchLicensePlatesPanel-Submit {
  /* display: flex; */
  text-align: center;
  display: block;
  align-items: center;
}

.OptionsPanel-SelectYear,
.OptionsPanel-SelectRegion {
  padding-bottom: 10px;
}
.SearchButton {
  margin-bottom: -10px;
  height: 35px;
  width: 20vw;
  text-align: center;
  font-size: small;
  border-radius: 5px;
  border: 1px solid black !important;

  color: var(--font-color-app) !important;
  background-color: var(--dark-color-app) !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !important;
